import  request  from '@/utils/request'

/**
 * @method addUserLogin 登录
 * @param {*} data
 * @returns
 */
export function addUserLogin(data) {
  return request({
    url: '/user-login',
    method: 'post',
    data
  })
}

/**
 * @method addUserInfo 用户信息
 * @param {*} data
 * @returns
 */
export function addUserInfo(data) {
  return request({
    url: '/user-info',
    method: 'post',
    data
  })
}

/**
 * @method getArriveTime 用获取到达时间
 * @param {*} data
 * @returns
 */
export const getArriveTime1 = (data) => {
  return request({
    url: `/attendance?pageNum=1&pageSize=10000&rdate=${data.year}%2F${data.month}%2F${data.day}&aid=${data.aid}`,
    method: 'GET',
    data
  })
}

export const getArriveTime = (data) => {
  return request({
    url: `/attendance?pageNum=1&pageSize=10000&rdate=2023%2F7%2F4&aid==${data.aid}`,
    method: 'GET',
    data
  })
}
